@import url(https://fonts.googleapis.com/css2?family=Marck+Script&family=Anton&display=swap);
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



/***************about section***************/
.about-section-container{
    position: relative; 
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.about-section-image-container {
    position: relative;
    z-index: 500;
    width: 100%;
    white-space: nowrap;
    margin-left: -50px;
    overflow: hidden;
}

.about-section-photo{
    position: relative;
    display: inline;
    height: 350px;
    margin: 50px 0 50px 50px;
    z-index: 500;
}

.about-section-candle{
    position: relative;
    display: inline;
    height: 150px;
    margin: 0px -100px 75px -50px;
    z-index: 100;
}

.about-section-table{
    position: relative;
    width: 100%;
    height: auto;
    margin: auto;
    margin-top: -150px;
    padding: 150px 0 0 0px;
    color: black;
    z-index: 400;
}

.about-section-table > h1 {
    font-family: 'Marck Script', cursive;
    font-weight: 600;
    font-size: 50px;
    margin-bottom: 15px;
}

.about-section-table > p {
    font-family: 'Anton', sans-serif;
    font-weight: 100;
    font-size: 16px;
    margin-bottom: 25px;
}

.about-section-table::before{
    content: "";
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    background-color: gray;
    opacity: .1;
}

/***************wood section***************/
.wood-section-container{
    position: relative; 
    height: 300px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.wood-section-container::before{
    content: "";
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    opacity: 0.8;
    background: url(/joy-and-serenity-candle/static/media/wood-bw.2b621688.jpg) no-repeat center center;
    background-size: cover;
}

.wood-section-content{
    position: relative;
    min-width: 350px;
}
/***************succulent section***************/
.succulent-section-container{
    position: relative;
    height: 300px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.succulent-section-container::before{
    content: "";
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    opacity: 0.7;
    background: url(/joy-and-serenity-candle/static/media/succulent-bw.ee69783d.png) no-repeat bottom right;
    background-size: contain;
}

.succulent-section-content{
    position: relative;
}
/***************product cards***************/
.product-card-container{
    display: flex;
    padding: 0px;
    margin: 0;
    flex-direction: column;
}
.product-card {
    display: flex;
    justify-content: flex-start;
    align-items:flex-end;
    flex-direction: column;
    padding: 0px;
    overflow: hidden;
    margin: 0px;
    height: 200px;
    width: 100%;
}
.product-card-footer,
.product-card-header,
.product-card-description {
    font-size: 50px;
    color: white;
    overflow: hidden;
}
* {
    margin: 0;
    padding: 0;
}

.app-container {
    text-align: center;
    background-color: white;
    height: 100vh;
}

html {
    scroll-behavior: smooth;
}

header {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 500px;
}

header::before {
    content:"";
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background: url(/joy-and-serenity-candle/static/media/logo-leaves-candle.83b8bdc4.jpg) no-repeat center center;
    background-size: cover;
    height: 500px;
    opacity: .4;
    width: 100%;
}

header h1 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
    font-size: 3.5rem;
    font-family: 'Marck Script', cursive;
    font-weight: 100;
    color: black;
    overflow: visible;
    word-spacing: -15px;
    letter-spacing: -3px;
    z-index: 1000;
    width: 100%;
}

.public-navbar-list {
    display: flex;
    width: 25vw;
    list-style-type: none;
    padding: 0;
    margin: 0;
    justify-content: space-around;
}

.public-navbar-link {
    color: black;
    text-decoration: none;
    font-size: .8rem;
    font-family: 'Anton', sans-serif;
    font-weight: 100;
    letter-spacing: 1px;
    background-color: transparent;
    margin: auto 25px;
}

.public-navbar-link:hover {
    color: rgba(0,0,0,.6);
}

nav {
    display: flex;
    justify-content: center;
    padding:2px 0;
    width: 100vw;
    min-width: 350px;
    background-color:white;
    margin: auto;
}

.public-nav{
    display: flex;
    justify-content: center;
    background: white;
    max-height: 3rem;
    padding: 0;
    margin: 0;
}

hr {
    border-top: solid black 1px;
}

hr.gray {
    border-top: solid gray 1px;
}

main{
    background-color: white;
    margin: 0;
    border-radius: 20px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h1, p{
    padding:0;
    margin:0;
}

footer {
    color: black;
    justify-content: center;
    margin: auto;
    padding: 50px;
}

.login-link {
    justify-content: flex-end;
}
